if (!Uint32Array.prototype.fill) {
  Object.defineProperty(Uint32Array.prototype, 'fill', {value: function(value, start, end) {
    start = start || 0;
    end = end || this.length;
    for (var i=start; i<end; i++) {
      this[i] = value;
    }
    return this;
  }});
}

if (!Uint32Array.prototype.slice) {
  Object.defineProperty(Uint32Array.prototype, 'slice', {value: function(start, end) {
    var i = start === undefined ? 0 : start;
    var finalIdx = end === undefined ? this.length : end;
    var count = finalIdx - i;
    var a = new this.constructor(count);
    var n = 0;
    while (i < finalIdx) {
      a[n] = this[i];
      ++n;
      ++i;
    }
    return a;
  }});
}



