export function updateNodeTransform( store, nodeId, newTransform ) {
  var parentNodeId = findParent(store, nodeId);

  //const parent = store.getIn(['sceneGraph',parentNode.get('id'),'parent']);
  var parentWorldTransform = getWorldTransform(store, parentNodeId);
  //console.log( 'parentWorldTransform', parentWorldTransform );

    var position = new THREE.Vector3(), quaternion = new THREE.Quaternion(), scale = new THREE.Vector3();

    var tempTransform = new THREE.Matrix4();
    tempTransform.copy( newTransform );

    if( parentWorldTransform ) {
      var invParentWorldTransform = new THREE.Matrix4();
      invParentWorldTransform.getInverse( parentWorldTransform, true );
      tempTransform.multiplyMatrices( invParentWorldTransform, tempTransform );
    }
    tempTransform.decompose( position, quaternion, scale );
    //console.log( 'position', JSON.stringify( position ) );
    //console.log( 'scale', JSON.stringify( scale ) );
    var euler = new THREE.Euler().setFromQuaternion( quaternion, 'ZYX' );
    //console.log( 'euler', JSON.stringify( euler ) );


    //let translation = node.getIn(['plugs','Transform',0,'translation']);
    //let rotation = node.getIn(['plugs','Transform',0,'rotation']);
    store.dispatch(
      set([nodeId,'Transform',0,'translation'],
        {x: position.x, y: position.y, z: position.z })
      );
    store.dispatch(
      set([nodeId,'Transform',0,'rotation'],
        {x: THREE.Math.radToDeg( euler.x ), y: THREE.Math.radToDeg( euler.y ), z: THREE.Math.radToDeg( euler.z ) })
      );
}

export function getEvalSceneGraph(store) {
  const sceneGraph = store.get('sceneGraph');

  if (sceneGraph._v === sceneGraph._ev) {
    return sceneGraph;
  }

  const sceneId = store.getIn(['sceneIO', 'id']); // getSceneId
  //console.log('updateSceneGraph', sceneGraph._ev, 'from', sceneGraph._v);
  evaluateSceneGraph(store, sceneId, sceneGraph);
  return sceneGraph;
};

const publicApi = {
  reducer,
  actions: { set, merge, multi, addNode, deleteNode, reparent, clone, setVRCamera, addOperator },
  selectors: { get, exportJSON, getVRCamera },
};
export default publicApi;

export {
  virtualCameraId as virtualCameraId,
  vrCameraId as vrCameraId,
  virtualTargetId as virtualTargetId,
};



