import * as THREE from 'three';
import ObjectArrayView from '../../generic/container/ObjectArrayView';
import ScalarArrayView from '../../generic/container/ScalarArrayView';
import Arrays from '../../generic/utility/Arrays';
import PolyMaps from '../model/PolyMaps';
import PolyMap from '../model/PolyMap';
import PolyMesh from '../model/PolyMesh';
import Skinning from '../model/Skinning';
import ImportConversions from '../algorithm/ImportConversions';

export default function Importer() {

  this.defaultFaceArity = 3; // triangles
  this.collapsePositions = true; // re-index positions
  this.collapseMapValues = true; // join equal map values at the same vertex

  this.faceArities = null;
  this.materialIds = null;

  this.mapIds = [];
  this.mapValues = {};
  this.mapIndices = {};

  this.skinning = null;
};

Importer.prototype = {

  constructor: Importer,

  setFaceArities: function( array,
      optionalNumberOfElements, optionalOffset, optionalStride ) {

    this.faceArities = new ScalarArrayView(
        optionalNumberOfElements || null,
        array,
        optionalOffset,
        optionalStride );

    return this;
  },

  setMaterialIds: function( array,
      optionalNumberOfElements, optionalOffset, optionalStride ) {

    this.materialIds = new ScalarArrayView(
        optionalNumberOfElements || null,
        array,
        optionalOffset,
        optionalStride );

    return this;
  },

  setMapValues: function( mapId, array,
      optionalNumberOfElements, optionalOffset, optionalStride ) {

    var id = PolyMaps.checkId( mapId ),
      ids = this.mapIds;

    if ( ids.indexOf( id ) === -1 ) ids.push( id );

    this.mapValues[ id ] = new ObjectArrayView(
          PolyMaps.getTypeInfo(
              PolyMaps.getAnchorName( mapId ) ).ElementType,
          optionalNumberOfElements || null,
          ! optionalOffset ? array : array.subarray( optionalOffset ),
          optionalStride );

    return this;
  },

  setMapIndices: function( mapId, array,
      optionalNumberOfElements, optionalOffset, optionalStride ) {

    this.mapIndices[ PolyMaps.checkId( mapId ) ] =
        new ScalarArrayView(
          optionalNumberOfElements || null,
          array,
          optionalOffset,
          optionalStride );

    return this;
  },

  setSkinning: function( positionSkinRange, skinWeights, skinBoneIndices, poseSkinToPoseBoneTransform, poseBoneToWorldTransform ) {
    this.skinning = {};

      this.skinning.positionSkinRange = positionSkinRange;
      this.skinning.skinWeights = skinWeights;
      this.skinning.skinBoneIndices = skinBoneIndices;

      this.skinning.poseSkinToPoseBoneTransform = new ObjectArrayView( THREE.Matrix4, null, poseSkinToPoseBoneTransform );
      this.skinning.poseBoneToWorldTransform = new ObjectArrayView( THREE.Matrix4, null, poseBoneToWorldTransform );

    return this;
  },


  toMeshAndClear: function() {

    var mapIds = this.mapIds,
      mapValueData = this.mapValues,
      mapIndexData = this.mapIndices,

      PositionMapId = PolyMaps.IdPositions,
      positions = mapValueData[ PositionMapId ],

      faceArities = this.faceArities,
      materialIds = this.materialIds;

    if ( ! positions )
      throw Error( "No positions!" );

    var positionIndices = mapIndexData[ PositionMapId ] || null,

      nFaceVertices = positionIndices ?
          positionIndices.length : positions.length;


    var faceOffsets = null;
    //  -----------

    if ( faceArities === null ) {

      var fixedArity = this.defaultFaceArity,
        nFaces = nFaceVertices / fixedArity | 0;

      faceArities = new Uint32Array( nFaces + 1 ).fill( fixedArity, 1 );

    } else {

      var nFaces = faceArities.length;
      faceArities = faceArities.toArray( Uint32Array, 1 );

    }

    // Here faceOffsets contains the face arities after a zero at index 0,
    // their accumulation yields the offsets (including length sentinel):
    faceOffsets = Arrays.accumulate( faceArities ); // (in-place)


    var indices = null;
    //  -------

    for ( var i = 0; i !== mapIds.length; ++ i ) {
      // avoid aliasing with the map indices in case it's the same array

      var mapId = mapIds[ i ];
      if ( mapId === PositionMapId ) continue;

      var mapIndexView = mapIndexData[ mapId ];

      if ( mapIndexView !== undefined )
        mapIndexView.forceRepack( Uint32Array );
    }

    if ( this.collapsePositions ) {
      // re-index values

      var indices = null;

      if ( positionIndices !== null )
        indices = positionIndices.repack( Uint32Array ).data;

      positions = ImportConversions.
          collapsePositions( positions, indices );

    } else if ( positionIndices !== null ) {
      // use given indices as-is
      indices = positionIndices.repack( Uint32Array ).data;
    }


    var mesh = PolyMesh.fromData(
    /*  ==== */ faceOffsets, positions, indices,
          materialIds && materialIds.repack( Uint32Array ).data );

    for ( var i = 0; i !== mapIds.length; ++ i ) {

      var mapId = mapIds[ i ];
      if ( mapId === PositionMapId ) continue;

      var mapIndexView = mapIndexData[ mapId ],
        mapValueView = mapValueData[ mapId ],

        polyMap = PolyMap.fromData(
            faceOffsets, mapIndexView.data, mapValueView );

      if ( this.collapseMapValues ) {

        ImportConversions.collapseMapVertexValues( mesh, polyMap );

        polyMap.compactValues();
      }

      PolyMaps.assignMap( mesh, mapId, polyMap );

    }

    if(this.skinning) mesh.skinning = new Skinning(this.skinning);


    Importer.call( this ); // re-run constructor to clear state

    return mesh;
  }

};



