import LZMA from 'lzma';
import LZMARaw from 'raw!lzmaRaw';
import LZMAWorker from 'raw!./lzmaWorker';

const workerQueue = [];
const workerPool = [];

let workerBlob;
try {
    workerBlob = new Blob([LZMARaw, LZMAWorker], {type: 'application/javascript'});
} catch (e) { // Backwards-compatibility
  if (__BROWSER__) {
    window.BlobBuilder = window.BlobBuilder || window.WebKitBlobBuilder || window.MozBlobBuilder;
    workerBlob = new BlobBuilder();
    workerBlob.append(LZMARaw);
    workerBlob.append(LZMAWorker);
    workerBlob = blob.getBlob();
  }
}

const workerLimit = __BROWSER__ ? Math.max(window.navigator.hardwareConcurrency - 1, 1) : 0;

function requeueWorker(idx){
  if(workerQueue.length>0){
    let toBeDecompressed = workerQueue.shift();
    toBeDecompressed.meta.idx = idx;
    workerPool[idx].postMessage(toBeDecompressed);
  } else {
    workerPool[idx].postMessage('CLOSE');
    delete workerPool[idx];
  }
}

export function lzmaDecompressor(response, meta, onSuccessEnd, onFailure){
  let freeWorkerIndex = -1;
  const lzmaWorkerObj = workerPool.find((worker, idx)=>{
    if(!worker) freeWorkerIndex = idx;
    return !worker;
  });
  const numWorkers = workerPool.length;

  if(workerQueue.length>0 || (numWorkers>=workerLimit && freeWorkerIndex===-1)){
    workerQueue.push({
      buffer: response,
      meta
    });
  } else {
    if(Worker){
      const lzmaWorker = new Worker(URL.createObjectURL(workerBlob));
      const index = freeWorkerIndex===-1 ? workerPool.length : freeWorkerIndex;
      lzmaWorker.onmessage = function(event) {
        if(event.data.type === 'SUCCESS'){
          onSuccessEnd(event.data.buffer, event.data.meta);
          requeueWorker(event.data.meta.idx);
        } else {
          onFailure(event.data.error, event.data.meta);
        }
      };
      lzmaWorker.postMessage({
        buffer: response,
        meta:{...meta, idx: index,}
      });
      workerPool[index] = lzmaWorker;
    } else {
      var inStream = {
        data: new Uint8Array(response),
        offset: 0,
        readByte: function() {
          return this.data[this.offset++]
        },
      }
      var outStream = {
        data:[],
        offset: 0,
        writeByte: function(val) {
          this.data[this.offset++] = val
        },
      }
      try{
        LZMA.decompressFile(inStream, outStream);
        var uintGeomArray = new Uint8Array(outStream.data);
        onSuccessEnd({
          buffer: uintGeomArray.buffer,
          meta,
        });
      } catch(e) {
        onFailure(e);
      }
    }
  }
}



