import * as THREE from 'three';
import ObjectArrayView from '../../generic/container/ObjectArrayView';

export default function Skinning( from ) {

  this.positionSkinRange = from.positionSkinRange;  //Uint32Array
  this.skinWeights = from.skinWeights;        //Float32Array
  this.skinBoneIndices = from.skinBoneIndices;    //Uint16Array

  this.poseSkinToPoseBoneTransform = from.poseSkinToPoseBoneTransform;  //ObjectArrayView of Matrix4's
  this.poseBoneToWorldTransform = from.poseBoneToWorldTransform;      //ObjectArrayView of Matrix4's

};

Skinning.fromData = function( positionSkinRange, skinWeights, skinBoneIndices ) {

  if ( result.skinWeights.length !== result.skinBoneIndices.length )
    throw Error( "skinWeights and skinBoneIndices sizes do not match." );

  const result = new Skinning( {

    positionSkinRange: positionSkinRange,
    skinWeights: skinWeights,
    skinBoneIndices: skinBoneIndices,

    poseSkinToPoseBoneTransform: poseSkinToPoseBoneTransform,
    poseBoneToWorldTransform: poseBoneToWorldTransform

  } );


  return result;
};



Skinning.prototype = {

  constructor: Skinning,

  applyTransforms: function(positions, skinToWorldTransform, boneWorldTransforms, rootBoneIndex) {
    return this.deform(this.createFinalTransforms(skinToWorldTransform, boneWorldTransforms, rootBoneIndex), positions);
  },

  createFinalTransforms: function( skinToWorldTransform, boneWorldTransforms, rootBoneIndex) {

    const poseSkinToPoseBoneTransform = this.poseSkinToPoseBoneTransform;
    const worldToSkinTransform = new THREE.Matrix4().getInverse( skinToWorldTransform, true );
    const finalTransforms = new Array( poseSkinToPoseBoneTransform.length );

    const identity = new THREE.Matrix4().identity();

    if( poseSkinToPoseBoneTransform.length === 0 ) {
      return finalTransforms;
    }

    let rootToWorldTransform;
    let worldToRootTransform;
    let poseRootToSkinTransform;

    if( rootBoneIndex > -1) {
      rootToWorldTransform = boneWorldTransforms[rootBoneIndex];
      worldToRootTransform = new THREE.Matrix4().getInverse( rootToWorldTransform, true );
      poseRootToSkinTransform = new THREE.Matrix4().getInverse( poseSkinToPoseBoneTransform.getAt(rootBoneIndex), true );
    }

    const boneToRootTransform = new THREE.Matrix4();
    const boneToSkinTransform = new THREE.Matrix4();
    const finalTransform = new THREE.Matrix4();

    for( let i = 0, il = poseSkinToPoseBoneTransform.length; i < il; i ++ ) {


      let boneToWorldTransform = boneWorldTransforms[i];
      if( rootBoneIndex > -1) {
        boneToRootTransform.multiplyMatrices( worldToRootTransform, boneToWorldTransform );
        boneToSkinTransform.multiplyMatrices( poseRootToSkinTransform, boneToRootTransform );
      } else {
        boneToSkinTransform.multiplyMatrices( worldToSkinTransform, boneToWorldTransform );
      }

      finalTransforms[i] = new THREE.Matrix4().multiplyMatrices( boneToSkinTransform, poseSkinToPoseBoneTransform.getAt(i) );

    }

    return finalTransforms;

  },

  deform: function ( finalTransforms, positions ) {

    const positionSkinRange = this.positionSkinRange;
    const skinWeights = this.skinWeights;
    const skinBoneIndices = this.skinBoneIndices;

    const deformedPositions = new ObjectArrayView(THREE.Vector3, positions.length);
    const v = new THREE.Vector3();
    const d = new THREE.Vector3();

    for(let i = 0; i<positions.length; i++){
      positions.getAt(i, v);
      d.multiplyScalar(0);
      for(let j = positionSkinRange[i]; j<positionSkinRange[i+1]; j++){
        positions.getAt(i, v);
        v.applyMatrix4( finalTransforms[ skinBoneIndices[j] ] );
        v.multiplyScalar( skinWeights[j] );
        d.addVectors(d, v);
      }
      if(positionSkinRange[i+1] - positionSkinRange[i] === 0){
        d.copy(v);
      }
      deformedPositions.setAt(i, d);
    }
    return deformedPositions;
  }

};



