import * as THREE from 'three';
import { getWorldTransform, get } from '../modules/scene';
import { getCamera } from '../modules/player';
import getProjectionMatrix from './utils/getProjectionMatrix';

function getScreenPositionNormal(nodeWorldMatrix, cameraWorldMatrix,
    cameraProjection, optionalLocalDirection) {

  const cameraInverseWorldMatrix = new THREE.Matrix4().getInverse(cameraWorldMatrix);

  const combinedCameraSpaceMatrix = new THREE.Matrix4().multiply(cameraInverseWorldMatrix).multiply(nodeWorldMatrix);
  const combinedProjectionMatrix = new THREE.Matrix4().multiply(cameraProjection).multiply(cameraInverseWorldMatrix).multiply(nodeWorldMatrix);

  const nodeLocalPosition = new THREE.Vector3(0, 0, 0);
  const nodeLocalDirectionOffset = optionalLocalDirection || new THREE.Vector3(0, 0, -1);

  const nodeCameraPosition = nodeLocalPosition.clone()
      .applyProjection(combinedCameraSpaceMatrix);
  const nodeCameraDirectionOffset = nodeLocalDirectionOffset.clone()
      .applyProjection(combinedCameraSpaceMatrix);

  const nodeCameraNormal = new THREE.Vector3()
      .subVectors(nodeCameraDirectionOffset, nodeCameraPosition).normalize();

  const nodeScreenPosition = nodeLocalPosition.clone()
      .applyProjection(combinedProjectionMatrix);
  nodeScreenPosition.addScalar(1.0).multiplyScalar(0.5);

  return {
    position: nodeScreenPosition,
    normal: nodeCameraNormal,
  };
}

export function getScreenPositionNormalAlpha(id, calcDirection, directionTarget) {
  return (store) => {
    const nodeWorldTransform = getWorldTransform(store, id);
    const cameraId = getCamera(store);
    const cameraPlug = get(store, { id: cameraId, evalPlug: 'Camera' });

    const cameraWorldTransform = getWorldTransform(store, cameraId);
    if (!cameraWorldTransform) return {};

    const cameraProjection = getProjectionMatrix(cameraPlug);

    const positionNormal = getScreenPositionNormal(nodeWorldTransform,
        cameraWorldTransform, cameraProjection, calcDirection && getDirection(store, id, directionTarget));

    const annotation = get(store, { id, evalPlug: 'Annotation' });

    let alpha = 1.0;

    // mis-spelled visibility property.
    if (annotation.limitVisilibility) {
      const visibilityAngle = Math.min(annotation.visibilityAngle * 0.5, 90);
      const cameraWorldDirection = new THREE.Vector3(0, 0, -1);
      const dotResult = cameraWorldDirection.dot(positionNormal.normal);
      if (dotResult < 0.0) {
        alpha = 0.0;
      } else {
        const theta = THREE.Math.radToDeg(
          Math.acos(cameraWorldDirection.dot(positionNormal.normal))
        );

        if (theta > visibilityAngle) {
          alpha = 0.0;
        } else if (theta > visibilityAngle * 0.75) {
          alpha = (visibilityAngle - theta) / (visibilityAngle * 0.25);
        }
      }
    }

    return { alpha, ...positionNormal };
  };
}


function getDirection(store, id, directionTarget) {
  let targetPosition = new THREE.Vector3(0, 0, 0);
  if (directionTarget) {
    targetPosition = new THREE.Vector3().setFromMatrixPosition(getWorldTransform(store, directionTarget));
  }
  const currentTransform = getWorldTransform(store, id);
  const currentPosition = new THREE.Vector3().setFromMatrixPosition(currentTransform);
  const direction = targetPosition.clone().sub(currentPosition).normalize();
  const inverseWorldMatrix = new THREE.Matrix4();
  inverseWorldMatrix.getInverse(currentTransform);
  direction.applyMatrix4(inverseWorldMatrix).normalize();
  return direction;
}



