import * as THREE from 'three';
import 'webvr-polyfill';
import '../utils/vrEffect'; // Extend three with VREffect
import '../utils/viveController';
import '../utils/objLoader';
import '../utils/reticle';
import '../utils/oculusController';

import { disableDeviceTracking, enableDeviceTracking,
  getCamera, getPrevCamera, isVRMode, RENDER_MODES,
  setRenderMode, useCamera, useVrCamera, } from '../../modules/player';
import { MOUSE_CONTROLS, updateCommands, updateCommand, activateCommand, deactivateCommand, setCommandOptions, getCommands } from '../../modules/commands';
import decompose from '../utils/decompose';
import '../utils/vrControls'; // extend three with VRControls
import getTargetWorldMatrix from '../utils/getTargetWorldMatrix';
import updateCameraLookAtTarget from '../utils/updateCameraLookAtTarget';
import { merge, vrCameraId, get, virtualTargetId } from '../../modules/sceneGraph';
import { get as sceneGet, set as sceneSet, getWorldTransform } from '../../modules/scene';

export default function (store, translator) {
  let vrEffect, vrDisplay, vrReticle, vrFloor, vrGrid, vrControls, hasExternalDisplay,
    controller1, controller2, oculusController, controllerHandler, mobileNavHandler, threeVrCamera,
    prevMoveMode, controllersAdded=false;

  const scene = translator.scene;
  const camera = translator.camera;

  let vrCapable = false;
  if(__BROWSER__){
    window.navigator.getVRDisplays().then((displays)=>{
      if( !displays.length ) return;
      vrCapable = displays[0].capabilities.canPresent;
      hasExternalDisplay = displays[0].capabilities.hasExternalDisplay;
      store.dispatch(updateCommand('vrMode', {enabled: vrCapable}))
    });
  }


  function controllerNavHandle(manipulatorsObject) {
    return (ev) => {
      if (!vrReticle.armObject) return;

      var pos = vrReticle.getReticlePosition().clone();
      pos.y = 0;
      var vrdpPos = threeVrCamera.position.clone();
      var offset = pos.addScaledVector(vrControls.offset, 2).sub(vrdpPos);
      offset.y=0;
      vrControls.setOffset(offset);
    }
  }

  function navHandle(manipulatorsObject) {
    return (ev) => {
      if (!vrReticle.armObject) return;

      const cameraId = getCamera(store);
      var pos = vrReticle.getReticlePosition().clone();
      pos.y = 0;
      manipulatorsObject.position.add(pos);
      manipulatorsObject.updateMatrix();

      const newCameraPosition = manipulatorsObject.position.clone();
      vrControls.setOffset(newCameraPosition);
      //store.dispatch(merge([cameraId, 'Transform', 0], decompose(newCameraPosition)));
    }
  }

  function addControllers(manipulatorsObject){
    var loader = new THREE.OBJLoader();
    manipulatorsObject.add(controller1);
    manipulatorsObject.add(controller2);
    loader.setPath( 'models/' );
    loader.load('vr_controller_vive_1_5.obj', (object) => {
      var loader = new THREE.TextureLoader();
      loader.setPath('models/');

      var controller = object.children[0];
      var material = new THREE.MeshBasicMaterial();
      material.map = loader.load('onepointfive_texture.png');
      material.specularMap = loader.load('onepointfive_spec.png');
      controller.material = material;

      controller1.add(object.clone());
      controller2.add(object.clone());
    });
    controllersAdded = true;
  }

  return {
    label: 'VR mode',
    activeLabel: 'Exit VR Mode',

    enabled: vrCapable,
    display: {
      playerTools: true,
    },
    options: {
      planeEnabled: true,
    },

    isActive: isVRMode,

    widget: {
      draw: function(manipulatorsObject) {
        const cbgeometry = new THREE.PlaneGeometry(20, 20, 1, 1);
        const mat = new THREE.MeshBasicMaterial({ color: 0x666666 });
        mat.transparent = true;//!this.options.planeEnabled;
        mat.opacity = this.options.planeEnabled ? 1.0 : 0.0;
        vrFloor = new THREE.Mesh(cbgeometry, mat);
        vrFloor.rotation.x = -Math.PI * 0.5;

        vrReticle = new THREE.Reticle(camera);
        vrReticle.addCollider(vrFloor);

        manipulatorsObject.add(vrReticle.armObject);
        manipulatorsObject.add(vrReticle.dot);
        manipulatorsObject.add(vrFloor);

        // This floor grid is __UGLY__!
        vrGrid = floorGrid();
        manipulatorsObject.add(vrGrid);

        console.log('active', hasExternalDisplay);
        if (!hasExternalDisplay) {
          //scene.add(camera);
          mobileNavHandler = navHandle(manipulatorsObject);
          window.addEventListener('touchend', mobileNavHandler)
        } else {
          //------Add vive controllers-----------
          if(controller1 && controller2 && !controllersAdded) addControllers(manipulatorsObject);

          controllerHandler = controllerNavHandle(manipulatorsObject);
          window.addEventListener("thumbpadup", controllerHandler);
        }
      },

      remove: function(manipulatorsObject) {
        if (!hasExternalDisplay) {
          scene.remove(camera);
          window.removeEventListener("touchend", mobileNavHandler);
        } else {
          window.removeEventListener("thumbpadup", controllerHandler);
        }
      },

      position: function(manipulatorsObject) {
        if(vrControls) {
          vrControls.update();
          manipulatorsObject.position.copy(vrControls.offset);
        }

        if(threeVrCamera){
          threeVrCamera.updateMatrix();
          matchToCamera(store, threeVrCamera);
        }

        vrReticle.update(manipulatorsObject);

        if(controller1 && controller2 && !controllersAdded) addControllers(manipulatorsObject);

        if (hasExternalDisplay && controller1 && controller2) {
          controller1.update();
          controller2.update();
          oculusController.update();
          controller1.position.applyQuaternion(vrControls.rotationalOffset);
          controller2.position.applyQuaternion(vrControls.rotationalOffset);
          controller1.quaternion.premultiply(vrControls.rotationalOffset);
          controller2.quaternion.premultiply(vrControls.rotationalOffset);
          controller1.updateMatrix();
          controller2.updateMatrix();
        }

        const downVector = new THREE.Vector3(0, 0, -1).applyQuaternion(camera.quaternion);
        const clampedY = Math.min(downVector.y, 0);
        const opacity = 1-Math.sqrt(1-clampedY*clampedY);
        if(this.options.planeEnabled) vrFloor.material.opacity = opacity;
        vrGrid.material.opacity = opacity;
        manipulatorsObject.updateMatrix();
      },
    },

    activate: () => {
      const translator = store.getTranslator();
      return window.navigator.getVRDisplays().then(function (displays) {
        if (displays.length <= 0) throw new Error('No VR Displays');

        if (displays.length > 0) {
          vrDisplay = displays[0];
          vrEffect = new THREE.VREffect(translator.renderer, vrDisplay);
          translator.externals.vrEffect = vrEffect;

          //hasExternalDisplay = vrDisplay.capabilities.hasExternalDisplay;
          if(!vrDisplay.capabilities.canPresent) return;

          const cameraId = getCamera(store);
          const cameraPosition = new THREE.Vector3().setFromMatrixPosition(
            getWorldTransform(store, cameraId)
          );
          cameraPosition.y=0;

          const targetPosition = new THREE.Vector3().setFromMatrixPosition(
            getTargetWorldMatrix(store, cameraId)
          ).sub(cameraPosition)
          targetPosition.y=0;
          targetPosition.normalize();
          const lookDirection = new THREE.Quaternion().setFromUnitVectors(new THREE.Vector3(0,0,-1), targetPosition);

          threeVrCamera = translator.camera.clone();

          vrControls = new THREE.VRControls(threeVrCamera);

          store.dispatch(useVrCamera(true));
          // If we have external display, then use vive controls?
          return vrEffect.requestPresent().then(() => {
            store.dispatch(activateCommand('firstPersonMode'));
            prevMoveMode = getCommands(store).firstPersonMode.options.movementMode;
            store.dispatch(setCommandOptions('firstPersonMode', {movementMode: 'plane'}));

            vrControls.setOffset(cameraPosition);
            vrControls.setRotationalOffset(lookDirection);
            translator.externals.vrControls = vrControls;
            vrControls.standing = true;
            vrControls.resetPose(); // zero orientation controls

            vrControls.update();
            threeVrCamera.updateMatrix();
            matchToCamera(store, threeVrCamera);

            if(hasExternalDisplay){
              controller1 = new THREE.ViveController( 0 );
              controller1.standingMatrix = vrControls.getStandingMatrix();

              controller2 = new THREE.ViveController( 1 );
              controller2.standingMatrix = vrControls.getStandingMatrix();

              oculusController = new THREE.OculusController( 0 );
            }


            store.dispatch(setRenderMode(RENDER_MODES.vr));
            store.dispatch(updateCommands(MOUSE_CONTROLS, { enabled: false }));

            store.setRequestAnimationFrame(vrDisplay.requestAnimationFrame, vrDisplay)

            return vrDisplay;

          }).catch((err) => {
            console.log('Error Activating VR');
            throw err;
          });
        }
      });
    },

    deactivate: () => {
      const translator = store.getTranslator();

      vrEffect.exitPresent();
      store.setRequestAnimationFrame();

      store.dispatch(deactivateCommand('firstPersonMode'));
      store.dispatch(setCommandOptions('firstPersonMode', {movementMode: prevMoveMode}));

      store.dispatch(setRenderMode(RENDER_MODES.normal));
      store.dispatch(updateCommands(MOUSE_CONTROLS, { enabled: true }));
      store.dispatch(useVrCamera(false));

      vrDisplay = null;

      /*const prevCameraId = getPrevCamera(store);
      if (prevCameraId) store.dispatch(useCamera(prevCameraId));*/

      if(!hasExternalDisplay) {
        //scene.remove(camera);
        window.removeEventListener("touchend", mobileNavHandler);
      } else {
        controller1 = null;
        controller2 = null;
        oculusController = null;
        controllersAdded = false;
        //scene.position.set(0, 0, 0);
        //vrElements.position.set(0, 0, 0);
        window.removeEventListener("thumbpadup", controllerHandler);
      }
      vrControls.standing = false;
      vrReticle.removeCollider(vrFloor);
      translator.renderer.setScissorTest(false);
      //vrElements.children = [];
      //scene.remove(vrElements);
    },
  };
}

function floorGrid() {
  // Materials
  var mat = new THREE.LineBasicMaterial( {color: 0x00ffff, linewidth: 5, depthTest:true} );
  mat.transparent = true;

  let lineGeometry = new THREE.BufferGeometry();
  let positions = new Float32Array( 80 * 2 * 3 );

  for(let x = -10, i = 0; x < 10; x+=0.5, i++) {
    positions[i*6+0] = x;
    positions[i*6+1] = 0;
    positions[i*6+2] = -10;
    positions[i*6+3+0] = x;
    positions[i*6+3+1] = 0;
    positions[i*6+3+2] = 10;
  }
  for(let z = -10, i = 40; z < 10; z+=0.5, i++) {
    positions[i*6+0] = -10;
    positions[i*6+1] = 0;
    positions[i*6+2] = z;
    positions[i*6+3+0] = 10;
    positions[i*6+3+1] = 0;
    positions[i*6+3+2] = z;
  }

  lineGeometry.addAttribute( 'position', new THREE.BufferAttribute( positions, 3 ) );

  let mesh = new THREE.LineSegments( lineGeometry, mat );
  mesh.mode = THREE.LineSegments;

  return mesh;
}

function matchToCamera(store, threeCamera){
  const cameraId = getCamera(store);
  const targetWorldMatrix = getTargetWorldMatrix(store);
  const targetPosition = new THREE.Vector3().setFromMatrixPosition(targetWorldMatrix);

  const length = targetPosition.sub(threeCamera.position).length();
  targetPosition.set(0,0,-length).applyQuaternion(threeCamera.quaternion).add(threeCamera.position);
  const upPosition = new THREE.Vector3(0,1,0).applyQuaternion(threeCamera.quaternion);

  targetWorldMatrix.setPosition(targetPosition);
  const camData = sceneGet(store, {id:cameraId, evalPlug:'Camera'})
  camData.upPosition = upPosition;
  store.dispatch(merge([cameraId, 'Transform',0], decompose(threeCamera.matrix.clone())));
  if (!sceneGet(store, { id: cameraId, evalPlug: 'Transform' }).target) {
    store.dispatch(merge([virtualTargetId, 'Transform', 0], decompose(targetWorldMatrix)));
  }
}



