import * as THREE from 'three';
import ObjectArrayView from '../../generic/container/ObjectArrayView';
import Lathe from './Lathe';

export default function Cone( radius, segments, height, thetaStart, thetaLength) {

  radius = radius || 50;

  segments = Math.max( 1, Math.floor( segments || 8 ) );
  const segmentLength = radius/segments;
  const heightSegmentLength = height/segments;

  thetaStart = THREE.Math.clamp( thetaStart || 0, 0, Math.PI * 2 );
  thetaLength = THREE.Math.clamp( thetaLength || Math.PI * 2, 0, Math.PI * 2 - thetaStart );

  const points = new ObjectArrayView(THREE.Vector3, (segments + 1)*2);
  const norms  = new ObjectArrayView(THREE.Vector3, (segments + 1)*2);
  const normPt = new THREE.Vector3(0,-1,0);
  const pt = new THREE.Vector3();

  for( let i = 0, il = segments; i <= il; i ++ ) {

    pt.set(i*segmentLength, -height/2, 0);

    points.setAt( i, pt );
    norms.setAt( i, normPt );
  }

  normPt.set(height, radius, 0);
  normPt.normalize();
  for( let i = 0, il = segments; i <= il; i++ ) {

    pt.set(radius-i*segmentLength, i*heightSegmentLength - (height/2), 0);

    points.setAt( segments+1+i, pt );
    norms.setAt( segments+1+i, normPt );
  }

  const latheSegments = segments < 3 ? 3 : segments;
  return Lathe( points, norms, latheSegments, thetaStart, thetaLength );
}



